import { createSelector } from '@reduxjs/toolkit';

export const selectContacts = state => state.contacts.items; //    selectContacts,        Redux (    useSelector)

export const selectIsLoading = state => state.contacts.isLoading; //    selectIsLoading,          Redux (    useSelector)

export const selectError = state => state.contacts.error; //    selectError,     '       Redux (    useSelector)

export const selectFiltered = state => state.filter; //    selectFilter,     (,    )    Redux (    useSelector)

export const selectVisibleContact = createSelector(
  [selectContacts, selectFiltered],
  (contacts, filter) => {
    const normalizedFilter = filter.toLowerCase().trim();
    return contacts.filter(contact =>
      contact.name.toLowerCase().includes(normalizedFilter)
    );
  }
);

export const selectEditContact = state => state.contacts.editContact;

export const selectEditModal = state => state.contacts.showModal;
